      </section>

      <section v-if="tela === 'inicio'" id="inicio">
        
        <Formulario defina="Defina a palavra" :action="setPalavra" v-if="etapa == 'palavra' " title="Palavra para seu amigo ao lado:" button="Próximo"/>
         <button class="give_up" v-on:click="reset">Voltar</button>

      </section>


      <section v-if="tela === 'inicio'" id="jogo">
      
      <Formulario defina="Defina a dica" :action="setDica" v-if="etapa == 'dica' " title="Uma palavra pra ajudar seu amigo" button="Iniciar jogo"/>
       <button class="give_up" v-on:click="reset">Voltar</button>
      </section>  

      <section v-if="tela === 'jogo'">
       
       <Jogo :jogarNovamente="jogarNovamente" :erros="erros" :palavra="palavra" :dica="dica" :verificarLetra="verificarLetra" :etapa="etapa" :letras="letras" :winsorfail="winsorfail" :jogar="jogar"/>
     
      </section>


