   {palavradavez:'notebook',dica:'Eletrônico'},
]


export default {
  name: 'App',
  data(){
    return{
      tela: 'escolha',
      etapa: 'escolha',
      palavra: '',
      dica: '',
      erros: 0,
      letras:[],
     palavras_sorteadas_tecnologia: palavras_sorteadas_tecnologia,
     palavras_sorteadas_animais: palavras_sorteadas_animais,
     palavras_sorteadas_objetos: palavras_sorteadas_objetos,
     palavras_sorteadas_profissao: palavras_sorteadas_profissao,
     selected: null,
     winsorfail: false,
     positive:false,
    }
  },
  components:{
    Formulario,
    Jogo,
    BotaoEscolherDica,
    EscolherModoDeJogo
  },

  methods:{
    setPalavra: function(palavra){
      this.palavra = palavra;
      this.etapa = 'dica';
    },
     setDica: function(dica){
      this.dica = dica;
      this.tela = 'jogo';
      this.etapa = 'jogo';
     },
     EscolherModo: function(modo,etapa){
       this.tela = modo
       this.etapa = etapa
     },
     setValues: function(values){
       
      switch(values){
         case 'tecnologia':
         this.sortearTecnologia()
         break;
        case 'animais': 
         this.sortearAnimais()
         break;
       case 'objetos':
         this.sortearObjetos()
         break;
       case 'profissao':
         this.sortearProfissao()
         break;
      } 
     },

     reset:function(){
       this.palavra = '';
       this.erros = '';
       this.erros = 0;
       this.letras = [];
       this.tela = 'escolha';
       this.etapa = 'escolha';
     },

     verificarLetra:function(letra){
       return this.letras.find(item => item.toLowerCase() === letra.toLowerCase());
     },
     jogar: function(letra){
       this.letras.push(letra);
       this.verificarErros(letra)
     },
     verificarErros: function(letra){
       //Acerto
       var errou = new Audio('./songs/wrong.mp3');
       var acertou = new Audio('./songs/contain.mp3');
       if(this.palavra.toLowerCase().indexOf(letra.toLowerCase()) >= 0){
         acertou.play();
         return this.verificarAcertos();
       }
       
       errou.play(); 
       this.erros++;
        //Enforcado
        if(this.erros == 6){
          this.etapa = 'enforcado';
          this.winsorfail = true;
        }
     },
     verificarAcertos: function(){
       let letrasUnicas = [...new Set(this.palavra.split(''))];
       if(letrasUnicas.length === (this.letras.length - this.erros)){
         this.winsorfail = false;
         this.etapa = 'ganhador';
         this.positive = true;
       }
     },
     jogarNovamente: function(){
       this.palavra = '';
       this.erros = '';
       this.erros = 0;
       this.letras = [];
       this.tela = 'escolha';
       this.etapa = 'escolha';
     },
     randomPalavra(Palavras){
       return Palavras[Math.floor(Math.random() * Palavras.length)];
     },

     sortearTecnologia(){
       this.selected = this.randomPalavra(this.palavras_sorteadas_tecnologia);
       this.tela = 'jogo'
       this.etapa = 'jogo'
       this.palavra = this.selected.palavradavez
       this.dica = this.selected.dica
     },
        sortearAnimais(){
       this.selected = this.randomPalavra(this.palavras_sorteadas_animais);
       this.tela = 'jogo'
       this.etapa = 'jogo'
       this.palavra = this.selected.palavradavez
       this.dica = this.selected.dica
     },
     sortearObjetos(){
       this.selected = this.randomPalavra(this.palavras_sorteadas_objetos);
       this.tela = 'jogo'
       this.etapa = 'jogo'
       this.palavra = this.selected.palavradavez
       this.dica = this.selected.dica
     },
       sortearProfissao(){
       this.selected = this.randomPalavra(this.palavras_sorteadas_profissao);
       this.tela = 'jogo'
       this.etapa = 'jogo'
       this.palavra = this.selected.palavradavez
       this.dica = this.selected.dica
     },
   
  }
 
